import { ReactElement, useState, MouseEvent } from 'react'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'
import Tooltip from '../Tooltip'
import { SecondaryButton } from '../Button'
import { FormattedMessage } from 'react-intl'
// import { version } from '../../../../package.json';
import config from './config'
import messages from './messages'
import {
  ActionsList,
  DrawerContainer,
  ItemIcon,
  ItemLabel,
  ListDivider,
  ListItemLink,
  ListWrapper,
  ProfileLink,
  ProfileWrapper,
  SignoutIcon,
  SignoutWrapper,
  Version,
} from './styles'

export type MessageKey = keyof typeof messages
type NavigationComponent = (props: { open: boolean }) => ReactElement

const NavigationDrawer: NavigationComponent = ({ open }) => {
  const [selectedService, setSelectedService] = useState<null | string>(null)

  const handleItemClick = (event: MouseEvent<HTMLElement>) => {
    const { currentTarget } = event
    setSelectedService(currentTarget.id)
  }

  const handleLogout = () => window.location.replace('/api/logout')

  const renderSignout = () => {
    if (open) {
      return (
        <SignoutWrapper open={open} container>
          <SecondaryButton onClick={handleLogout}>
            <FormattedMessage {...messages.signOut} />
          </SecondaryButton>
        </SignoutWrapper>
      )
    }
    return (
      <SignoutIcon data-testid="signout-icon" onClick={handleLogout}>
        <span className="icon-signout" />
      </SignoutIcon>
    )
  }

  const renderVersion = () => (
    <Version open={open} variant="caption">
      {open ? 'Version' : 'V'}
    </Version>
  )

  const renderProfileSection = () => (
    <Tooltip title={<FormattedMessage {...messages.accountAndSettings} />}>
      <ProfileLink to="#">
        <ItemIcon>
          <AccountCircleIcon style={{ fontSize: 32 }} />
        </ItemIcon>
        <ItemLabel
          primary={<FormattedMessage {...messages.accountAndSettings} />}
          color="textGrey"
        />
      </ProfileLink>
    </Tooltip>
  )

  const renderList = () =>
    config.map(({ iconClass, id, route }) => {
      const selected = selectedService === id
      const label = <FormattedMessage {...messages[id as MessageKey]} />
      console.log(route)
      return (
        <Tooltip title={label} key={id}>
          <ListItemLink
            to={route}
            onClick={handleItemClick}
            selected={selected}
            data-testid={`${id}-container`}
            id={id}
          >
            {iconClass && (
              <ItemIcon data-testid={`${id}-icon`}>
                <span className={iconClass} />
              </ItemIcon>
            )}
            <ItemLabel primary={label} data-testid={`${id}-text`} />
          </ListItemLink>
        </Tooltip>
      )
    })

  return (
    <DrawerContainer variant="permanent" open={open}>
      <ListWrapper>
        <ActionsList container direction="column" justify="space-around">
          {renderList()}
        </ActionsList>
        <ProfileWrapper
          open={open}
          container
          direction="column"
          justify="space-around"
        >
          <ListDivider />
          {renderProfileSection()}
          <Tooltip title={<FormattedMessage {...messages.signOut} />}>
            {renderSignout()}
          </Tooltip>
          {renderVersion()}
        </ProfileWrapper>
      </ListWrapper>
    </DrawerContainer>
  )
}

export default NavigationDrawer
