import Tooltip, { TooltipProps } from '@material-ui/core/Tooltip'
import { withStyles } from '@material-ui/core/styles'
import { ReactElement } from 'react'
import { getColor } from '../../utils/mixins'

type TooltipComponent = (props: TooltipProps) => ReactElement

const BlackTooltip = withStyles({
  tooltip: {
    color: 'white',
    backgroundColor: getColor('codGrey'),
  },
  arrow: {
    '&:before': {
      backgroundColor: getColor('codGrey'),
    },
  },
})(Tooltip)

const PrimaryTooltip: TooltipComponent = (props) => (
  <BlackTooltip arrow placement="right" {...props} />
)

export default PrimaryTooltip
