import { ReactElement } from 'react'
import { Box, IconButton, Grid } from '@material-ui/core'
import MenuIcon from '@material-ui/icons/Menu'
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import Tooltip from '../Tooltip'
import SearchIcon from '@material-ui/icons/Search'
import { FormattedMessage } from 'react-intl'
import DMGIcon from '../../images/logo.svg'
import SearchBox from '../SearchBox'
import {
  StyledAppBar,
  StyledToolbar,
  StyledNotShowItemInMobile,
  StyledIcon,
} from './styles'
import config from './config'
import messages from './messages'
import { useHistory } from 'react-router-dom'

type Props = {
  toggleNavigationDrawer: () => void
}
export type MessageKey = keyof typeof messages

const Navbar = ({ toggleNavigationDrawer }: Props): ReactElement => {
  const history = useHistory()

  const logoClick = () => {
    history.push('/')
  }

  const renderList = () =>
    config.map(({ iconClass, id }) => (
      <Tooltip
        title={<FormattedMessage {...messages[id as MessageKey]} />}
        placement="bottom"
        key={iconClass}
      >
        <Grid item>
          <StyledIcon data-testid={`${id}-icon`}>
            <span className={iconClass} />
          </StyledIcon>
        </Grid>
      </Tooltip>
    ))

  return (
    <StyledAppBar position="static">
      <Grid container alignItems="center">
        <Grid className="logo-grid" item xs={3} md={1} container wrap="nowrap">
          <StyledToolbar>
            <IconButton
              edge="start"
              color="inherit"
              data-testid="search"
              aria-label="menu"
              onClick={toggleNavigationDrawer}
            >
              <MenuIcon />
            </IconButton>
          </StyledToolbar>
          <span onClick={logoClick} role="button" tabIndex={0}>
            <img src={DMGIcon} alt="DMG icon" />
          </span>
        </Grid>
        <Grid item xs={1} />
        <StyledNotShowItemInMobile item xs={5} md={7}>
          <SearchBox
            startIcon={<SearchIcon />}
            endIcon={<KeyboardArrowDownIcon />}
          />
        </StyledNotShowItemInMobile>
        <StyledNotShowItemInMobile item xs={2} />
        <StyledNotShowItemInMobile item xs={1}>
          <Box pr={3}>
            <Grid justify="space-between" container>
              {renderList()}
            </Grid>
          </Box>
        </StyledNotShowItemInMobile>
      </Grid>
    </StyledAppBar>
  )
}

export default Navbar
