import styled from 'styled-components'
import { withTheme, Theme } from '@material-ui/core/styles'
import { Link, LinkProps } from 'react-router-dom'

import List from '@material-ui/core/List'
import ListItemText from '@material-ui/core/ListItemText'
import Divider from '@material-ui/core/Divider'
import Drawer from '@material-ui/core/Drawer'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import { getColor, getPaletteColor } from '../../utils/mixins'
import { smallNavbarHeight, navbarHeight } from '../Navbar/styles'

export const openDrawerWidth = '264px'
export const closedDrawerWidth = '64px'

type Props = { open?: boolean; theme: Theme }

const LinkItem = styled(Link)`
  &&& {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    text-decoration: none;

    :hover {
      background-color: ${getColor('skyBlue')};
      text-decoration: none;
    }
  }
`

export const ListItemLink = styled(LinkItem)`
  &&& {
    padding: 8px 0 8px 16px;
    background-color: ${({ selected }: LinkProps & { selected?: boolean }) =>
      selected ? getColor('skyBlue') : 'transparent'};
  }
`

export const ItemIcon = styled(ListItemIcon)`
  && {
    color: ${getColor('fadedBlack')};
    font-size: 32px;
    min-width: 0;
    margin-right: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`

export const SignoutIcon = styled(ItemIcon)`
  &&& {
    :hover {
      background-color: ${getColor('skyBlue')};
    }

    padding: 8px 0;
    width: 64px;
    cursor: pointer;
  }
`

export const ProfileLink = styled(LinkItem)`
  && {
    padding: 16px;
    font-size: 32px;
    cursor: pointer;

    @media (max-width: 540px) {
      padding: 6px 16px;
    }
  }
`

export const ItemLabel = styled(ListItemText)`
  && {
    margin: 0;
    color: ${(props: { color?: string }) =>
      getColor(props.color || 'fadedBlack')};
    height: 100%;
    display: flex;
    align-items: center;

    .MuiTypography-body1 {
      font-size: 14px;
      font-weight: 500;
      line-height: 120%;
      letter-spacing: 0.15px;
    }
  }
`

export const ListDivider = styled(Divider)`
  width: 100%;
`

export const ListWrapper = styled(List)`
  && {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    height: 100%;
  }
`

export const ActionsList = styled(Grid)`
  && {
    padding: 24px 0;
    height: 80%;
  }
`

export const ProfileWrapper = styled(Grid)`
  width: ${(props: { open: boolean }) =>
    props.open ? openDrawerWidth : closedDrawerWidth};
  flex: 1;
`

export const Version = styled(Typography)`
  && {
    display: flex;
    justify-content: ${(props: { open: boolean }) =>
      props.open ? 'flex-start' : 'center'};
    line-height: 166%;
    box-sizing: border-box;
    width: ${(props: { open: boolean }) =>
      props.open ? openDrawerWidth : closedDrawerWidth};
    letter-spacing: 0.4px;
    color: ${getColor('mediumGrey')};
    padding: 8px 16px;
  }
`

export const SignoutWrapper = styled(Grid)`
  && {
    padding: 8px 16px;
    font-size: 32px;
    width: ${(props: { open: boolean }) =>
      props.open ? openDrawerWidth : closedDrawerWidth};

    @media (max-width: 540px) {
      padding: 6px 16px;
    }
  }
`

export const DrawerContainer = withTheme(styled(Drawer)`
  .MuiDrawer-paper {
    top: ${navbarHeight};
    background-color: ${getPaletteColor('grey[50]')};
    width: ${(props: Props) =>
      props.open ? openDrawerWidth : closedDrawerWidth};
    flex-shrink: 0;
    white-space: nowrap;
    display: flex;
    justify-content: space-between;
    height: calc(100% - ${navbarHeight});
    overflow-x: hidden;
    box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2),
      0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);

    transition: ${({ open, theme }: Props) =>
      theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: open
          ? theme.transitions.duration.enteringScreen
          : theme.transitions.duration.leavingScreen,
      })};

    @media (max-width: 540px) {
      top: ${smallNavbarHeight};
      height: 896px;
      position: absolute;
    }
  }
`)
